.null {
  display: none;
}

.success {
  background-color: rgb(190, 190, 190);
  color: green;
  font-size: large;
  padding: 15px 30px;
  border-radius: 10px;
  border: 3px solid green;
  margin-bottom: 15px;
}

.error {
  background-color: rgb(190, 190, 190);
  color: red;
  font-size: large;
  padding: 15px 30px;
  border-radius: 10px;
  border: 3px solid red;
  margin-bottom: 15px;
}